body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    display: grid;
    grid-template-areas: 
    "title"
    "main"
    "time"
    "button"
    ;
    grid-template-rows: 10% 30% 15% 30%;
    grid-row-gap: 5%;
    height: 100vh;
    background: #F2F2F5;
    overflow: hidden;
}

h1.title{
    grid-area: title;
    font-size: 1.7em;
    margin-top: 1%;
}

.main-display-wrapper {
    box-sizing: content-box;
    position: relative;
    height: 100%;
    width: inherit;
 }

 .main-display {
    display: flex;
    height: 100%;
    flex-wrap: nowrap;
    position: absolute;
    width: 100vw;
    left: 42vw;
 }

 .next-button {
    font-size: 2em;
    margin: 0;
    border: 1px solid #202020;
    padding: 5px 15px;
    border-radius: 5px;
    background: lightgreen;
    position: absolute;
    right: 15%;
    bottom: 13%;
    border: 1px solid black;
 }

 .timer {
     align-self: center;
 }

 .timer h1 {
     font-size: 2em;
     margin: 0;
     border: 1px solid #202020;
     padding: 5px 15px;
     border-radius: 5px;
 }

 .buttons {
    display: flex;
    flex-direction: column;
 }

.justify-center {
    justify-self: center;
}

h1 {
    font-size: 1em;
}
h2 {
    font-size: 0.8em;
}
h3 {
    font-size: 0.6em;
}

.circle {
    border-radius: 50%;
    height: 70px;
    width: 70px;
    border: solid 8px white;
    text-align: center;
    overflow: hidden;
    background: transparent;
}

.circle img {
    height: 80px
}

.pause-btn {
    border: 0;
    background: transparent;
    box-sizing: border-box;
    width: 0;
    height: 74px;
    border-color: transparent transparent transparent #202020;
    -webkit-transition: 100ms all ease;
    transition: 100ms all ease;
    cursor: pointer;
    border-style: solid;
    border-width: 37px 0 37px 60px;
    padding: 0;
}

.pause-btn.playing {
border-style: double;
border-width: 0px 0 0px 60px;
}

.pause-btn:hover {
border-color: transparent transparent transparent #404040;
}

.center {
    text-align: center;
}

@media(min-width: 850px) {
    /* .App {
        display: grid;
        grid-template-areas: 
        "main"
        "timer"
        "button"
        ;
    } */
    
    h1.title{
        font-size: 3em;
        margin-top: 2.5%;
    }

    h1 {
        font-size: 2em;
    }
    h2 {
        font-size: 1.6em;
    }
    h3 {
        font-size: 1.2em;
    }
    
    .circle {
        border-radius: 50%;
        height: 150px;
        width: 150px;
        border: solid 8px white;
        text-align: center;
        overflow: hidden;
        background: transparent;
    }
    
    .circle img {
        height: 165px
    }

    .pause-btn {
        height: 150px;
        border-width: 75px 0 75px 125px;
    }
    .pause-btn.playing {
        border-width: 0px 0 0px 100px;
    }

    .timer h1 {
        font-size: 4em;
        padding: 10px 25px;
    }

    .next-button {
        font-size: 4em;
        padding: 10px 25px;
        right: 15%;
        bottom: 15%;
     }
}
.team-cover {
    /* Width determind in js style object */
    align-self: flex-end;
    display: flex;
    align-items: center;
    flex-direction: column;
    position: absolute;
}
